instance.defaults.timeout = 6000;

instance.interceptors.response.use(
  function (response) {
    store.commit("error/setValidationError", {}); //   ValidationError   .
    // store.commit("CollectionStore/setnoResult", false); //   noResult false .
    return response;
  },
  function (errors) {
    if (errors.response.status === 500) {
      store.commit("error/setValidationError", errors.response.data);
    } else if (errors.response.status === 502) {
      console.log(``);
    }
    // else if (errors.response.status === 404) {
    //   store.commit("error/setnoResult", true);
    // }
    else {
      return Promise.reject(errors); //   422     catch   .
    }
  }
);

export default instance; // instance      export 

